import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { RouterModule } from '@angular/router';
import { NgbTooltipModule } from '@ng-bootstrap/ng-bootstrap';
import { NgSelectModule } from '@ng-select/ng-select';
import { TranslateModule } from '@ngx-translate/core';
import { InlineSVGModule } from 'ng-inline-svg-2';
import { WidgetsModule } from 'src/app/theme/partials';
import { CreateCustomerPaymentComponent } from './create-customer-payment/create-customer-payment.component';
import { ManageCustomerPaymentComponent } from './manage-customer-payment/manage-customer-payment.component';
import { UpdateCustomerPaymentComponent } from './update-customer-payment/update-customer-payment.component';

@NgModule({
  declarations: [
    ManageCustomerPaymentComponent,
    CreateCustomerPaymentComponent,
    UpdateCustomerPaymentComponent,
  ],
  imports: [
    CommonModule,
    NgbTooltipModule,
    FormsModule,
    NgSelectModule,
    InlineSVGModule,
    ReactiveFormsModule,
    TranslateModule,
    RouterModule.forChild([
      {
        path: '',
        component: ManageCustomerPaymentComponent,
      },
      {
        path: 'create',
        component: CreateCustomerPaymentComponent,
      },
      {
        path: 'update/:id/:type',
        component: UpdateCustomerPaymentComponent,
      },
    ]),
    WidgetsModule,
  ],
})
export class PaymentModule {}
